#root {
	height: 100%;

	.skirmish {
		height: 100%;
		background-color: rgb(240, 240, 240);

		.top-bar {
			height: 50px;
			line-height: 50px;
			text-align: center;
			letter-spacing: 8px;
			text-transform: uppercase;
			font-size: 20px;
		}

		.content {
			height: calc(100% - 50px);
			padding: 15px;
			overflow-y: auto;
		}
	}
}

.playing-card {
	padding-top: 140%;
	perspective: 1000px;

	.playing-card-inner {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		transition: transform 0.5s;
		transform-style: preserve-3d;

		.playing-card-front,
		.playing-card-back {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			backface-visibility: hidden;
			border: 1px solid rgb(220, 220, 220);
			border-radius: 15px;
			cursor: pointer;
			overflow: auto;

			&:hover {
				border-color: #40a9ff;
			}
		}

		.playing-card-back {
			transform: rotateY(180deg);

			.playing-card-back-design {
				border-radius: 15px;
				height: 100%;
				background-color: rgb(220, 220, 220);
				color: #ffffff;
			}
		}
	}

	&.flipped .playing-card-inner {
		transform: rotateY(180deg);
	}
}
